<script>
import { getUrlParam } from "@/utils/param";
import App from "@/utils/cofigApp";
export default {
  data() {
    return {};
  },
  // computed: {
  //   appid() {
  //     return App.appid;
  //   },
  // },
  beforeCreate() {
    let _this = this;
    let code = getUrlParam("code");
    // console.log(app);
    // console.log(code);
    // console.log(App.appid)
    if (code) {
      this.$store
        .dispatch("wechat_auth", { code, appid: App.appid })
        .then((res) => {
          _this.$toast("未绑定手机号，需先绑定手机号");
          _this.$router.push({
            name: "mbind",
            params: { uuid: res.id, code: res.disposable_code },
          });
        })
        .catch((error) => {
          _this.$router.replace({ name: "m_account" });
        });
    } else {
      this.$toast("没有获取到code!");
      this.$router.replace("/");
    }
  },
  render: function (h) {
    return h(); // avoid warning message
  },
};
</script>



